import React, { useEffect, useState } from 'react';
import '../../App.css';
import { Link, useHistory } from 'react-router-dom'
import SideNavBar from '../../components/SideNavBar.js'
import Topbar from '../../components/Topbar.js'
import '../../css/settings.css';


function SettingsProfilePage() {

    const history = useHistory();
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));

    const [user, setUser] = useState({firstName: 'John',lastName: 'Smith', location: 'Athens, Greece', bio: 'Curious Individual'})

    // State for input fields
    const [fName, setFName] = useState("")
    const [lName, setLName] = useState("")
    const [username, setUsername] = useState("")
    const [bio, setBio] = useState("")
    const [loc, setLoc] = useState("")

    function placeHolder(load,attr){
        return user[attr];
    }

    function conditionalUpdate(stateItem,attr){
        if (stateItem === ""){
            return user[attr];
        }
        return stateItem;
    }

    return (
        <div>
            <Topbar/>
            <SideNavBar/>
            <div className="mainContent">
                
                <div className="jete-settingsMenu settings-postition">
                    <div className="list-group">
                        <a href="#" className="list-group-item list-group-item-action active">Profile</a>
				        <Link to='/settings/security' className="list-group-item list-group-item-action">Security</Link>
                        <Link to='/settings/billing' className="list-group-item list-group-item-action">Billing</Link>
                    </div>
                </div>
                
                <div className="settingsContains settings-postition">
                    <h1 className="settings-header">Public Profile</h1>
                    <hr className="new4"/>


                    <form className="settingsprofile-form">
                        <div className="form-group name">
                            <label for="settingsname">Name</label>
                            <input type="text" className="form-control" id="settingsname"  placeholder={placeHolder("name","firstName")} value={fName} onChange={e => setFName(e.target.value)}/>
                        </div>
                        <div className="form-group surname">
                            <label for="settingssurname">Last name</label>
                            <input type="text" className="form-control" id="settingssurname" aria-describedby="namehelp" placeholder={placeHolder("surname","lastName")} value={lName} onChange={e => setLName(e.target.value)}/>
                            <small id="surnamehelp" className="form-text text-muted">Your name and last name are visible in your public profile.</small>
                        </div>
                        <div className="form-group usernamesettings">
                            <label for="settingsname">Username</label>
                            <input type="text" className="form-control" id="settingsusername" aria-describedby="usernamehelp" placeholder={loggedUser.username} value={username} onChange={e => setUsername(e.target.value)}/>
                            <small id="usernamehelp" className="form-text text-muted"> Username needs to contain at lest 6 letters or digits</small>

                        </div>
                        
                        <div className="form-group pt-3 email">
                            <label className="sr-only" for="emailInput">Email</label>
                            <div className="input-group mb-2">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">@</div>
                                </div>
                                <input type="email" className="form-control" id="emailInput" placeholder={loggedUser.email} readOnly/>
                            </div>
                        </div>



                        <div className="form-group bio">
                            <label for="settingsbio">Bio</label>
                            {/* <input type="text" className="form-control" id="settingsbio" aria-describedby="biohelp" placeholder="Bio from backend"/> */}
                            <textarea className="form-control" id="settingsbio" placeholder={placeHolder("bio","bio")} value={bio} onChange={e => setBio(e.target.value)}/>
                            <small id="biohelp" className="form-text text-muted">Tell us about you</small>
                        </div>

                        <div className="form-group location">
                            <label for="locationname">Location</label>
                            <input type="text" className="form-control" id="locationname"  placeholder={placeHolder("location","location")} value={loc} onChange={e => setLoc(e.target.value)}/>
                        </div>

                        <button type="button" className="btn btn-success settingsprofile-button" onClick={() => console.log("change")}>Update Information</button>


                    </form>
                    <div className="blankspace"/>                            
                </div>
            </div>
        </div>
    );
}
 
export default SettingsProfilePage;