import React, {useState} from 'react';
import '../App.css';
import {Modal, Button} from 'react-bootstrap';
import { useHistory } from 'react-router-dom'
import AssignDev from './AssignDev.js'




function NewProjectPopUp(props){

    const history = useHistory();
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));

    const [devs, setDevs] = useState([])

    const [title, setTitle] = useState("")
    const [description, setDescription] = useState("")

    const [showEmptyTitle, setShowEmptyTitle] = useState(false)
    const [showEmptyDescription, setShowEmptyDescription] = useState(false)

    function submit() {
        let earlyExit = false
        if (title === ""){
            earlyExit = true;
            setShowEmptyTitle(true);
        }
        else {
            setShowEmptyTitle(false)
        }
        if (description === ""){
            earlyExit = true;
            setShowEmptyDescription(true);
        }
        else {
            setShowEmptyDescription(false)
        }
        if (earlyExit){
            return ;
        }

        props.onHide()

        setDevs([])
        setTitle("")
        setDescription("")
        setShowEmptyTitle(false)
        setShowEmptyDescription(false)
        
        
    }

    function clickExit(){
        props.onHide()

    }

    return (
        <div>
            <Modal
                {...props}
                size="md"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                    Create New Project
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <form className="row g-3">
                        <div className="col-12">
                            <label for="inputTittle" className="form-label">Project Title <span style={{color: "#cc0000"}}>*</span></label>
                            <input type="text" className="form-control" id="projectTittle" placeholder="Title" value={title} onChange={e => setTitle(e.target.value)}/>
                            <div>
                                { (showEmptyTitle) && <span className="badge bg-danger rounded-pill">Title cannot be empty</span>}
                            </div>
                        </div>
                        <div className="col-12">
                            <label for="inputDescription" className="form-label">Project Description <span style={{color: "#cc0000"}}>*</span></label>
                            <textarea type="text" className="form-control" id="projectDescription" placeholder="Description" value={description} onChange={e => setDescription(e.target.value)}/>
                            <div>
                                { (showEmptyDescription) && <span className="badge bg-danger rounded-pill">Description cannot be empty</span>}
                            </div>
                        </div>
                        <div className="col-12">
                            <label for="assignDev" className="form-label">Invite Developer</label>
                            <AssignDev devs={devs} setDevs={setDevs} message=" will be requested to join " checkForReAdd={false} projId={0}/>
                        </div>
                    </form>
                    <div className="row g-3 pt-3">
                        <div className="col-8">
                            <button className="btn btn-primary" onClick={() => submit()}>Confirm</button>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="outline-danger" onClick={clickExit}>Cancel</Button>
                </Modal.Footer>
            </Modal>
		</div>
    );
}
 
export default NewProjectPopUp;