import React, { useState, useEffect } from 'react';
import '../App.css';
import {Modal, Button} from 'react-bootstrap'
import AssignDev from './AssignDev.js'
import { useHistory } from 'react-router-dom'
import RedAsterisc from './RedAsterisc.js'


function IssuePopUp(props){

    const history = useHistory();
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));

    useEffect(() => {
        if (!loggedUser){
            history.push("/login");
        }
    },[props.projÎ™d])

    const [rawStories, setRawStories] = useState([{id: 0, title: "Story1"},{id: 0, title: "Story2"},{id: 0, title: "Story3"},{id: 0, title: "Story4"}])

    const issueTypes = ['Task','Story',"Epic"]
    const sprints = ['Current Sprint', 'Next Sprint', 'After next Sprint']

    const [title, setTitle] = useState("")
    const [description, setDescription] = useState("")
    const [selectedType, setIssTy] = useState('')
    const [selectedEpic, setSelectedEpic] = useState('')
    const [selectedSprint, setSelectedSprint] = useState('')
    const [selectedStory, setSelectedStory] = useState('')

    const [devs, setDevs] = useState([])

    function cancel() {
        props.onHide()
        setDevs([])
    }

    function getStory(){
        console.log('Looking for:')
        console.log(selectedStory)
        for (var i=0; i<rawStories.length; i++){
            if (selectedStory === rawStories[i].title){
                return rawStories[i]
            }
        }
        console.log('Story was not found')
        return -1
    }

    function getEpicId(){
        const epics = props.epics
        for (var i=0; i<epics.length; i++){
            if (selectedEpic === epics[i].title){
                return epics[i].id
            }
        }
        console.log('Epic was not found')
        return -1
    }

    function getSprintId(){
        const sprints = props.sprints
        if (selectedSprint === 'Current Sprint'){
            return sprints[0].id
        }
        if (selectedSprint === 'Next Sprint'){
            return sprints[1].id
        }
        if (selectedSprint === 'After next Sprint'){
            return sprints[2].id
        }
        console.log('Sprint was not found')
        return -1
    }

    const [showEmptyTitle, setShowEmptyTitle] = useState(false)
    const [showEmptyDescription, setShowEmptyDescription] = useState(false)

    function submit() {
        let earlyExit = false
        if (title === ""){
            earlyExit = true;
            setShowEmptyTitle(true);
        }
        else {
            setShowEmptyTitle(false)
        }
        if (description === ""){
            earlyExit = true;
            setShowEmptyDescription(true);
        }
        else {
            setShowEmptyDescription(false)
        }
        if (earlyExit){
            return ;
        }

        props.onHide()

        let fetchAdr = ''
        let body = ''
        if (selectedType === 'Task'){
            const story = getStory()
            fetchAdr = 'http://localhost:8080/projects/' + props.projId + '/sprints&epics/' + story.sprint_id + '&' + story.epic_id + '/stories/'+story.id+'/tasks/create'
            body = JSON.stringify({ title: title,
                                    description: description,
                                    status: 0,
                                    project_id: props.projId,
                                    epic_id: story.epic_id,
                                    sprint_id: story.sprint_id,
                                    story_id: story.id})
        }
        else if (selectedType === 'Story'){
            const sprint = getSprintId()
            const epic = getEpicId()
            fetchAdr = 'http://localhost:8080/projects/' + props.projId + '/sprints&epics/' + sprint + '&' + epic + '/stories/create'
            body = JSON.stringify({ title: title,
                                    description: description,
                                    status: 0,
                                    project_id: props.projId,
                                    epic_id: epic,
                                    sprint_id: sprint})
        }
        else if (selectedType === 'Epic'){
            fetchAdr = 'http://localhost:8080/projects/epics/create'
            body = JSON.stringify({  title: title,
                                    description: description,
                                    status: 0,
                                    project_id: props.projId })
        }
        else{
            console.log("pick one dummy")
            return
        }
        fetch(fetchAdr, {
            method: 'post', 
            headers: { Authorization: 'Bearer ' + loggedUser.accessToken,
                        'Content-Type': 'application/json' },
            body: body
        })
            .then(res => res.json())
            .then((data) => {
                console.log(data)
                setTitle("")
                setDescription("")
                if (data.id){
                    if (selectedType != 'Task'){
                        window.location.reload(false);
                    }
                }
                else {
                    console.log("Something went wrong while creating issue")
                }
                return data;
            })
            .then((data) => {
                if (selectedType === 'Task' && data.id){
                    var i=0;
                    var numResponses = 0;
                    for (; i < devs.length; i++){ 
                        console.log("assigning " + devs[i].username + " with id " + devs[i].id + " to task with id " + data.id);
                        fetch('http://localhost:8080/assignees/', { 
                            method: 'post', 
                            headers: { Authorization: 'Bearer ' + loggedUser.accessToken,
                                'Content-Type': 'application/json'  },
                            body: JSON.stringify({  user_id: devs[i].id,
                                        project_id: data.project_id,
                                        epic_id: data.epic_id,
                                        sprint_id: data.sprint_id,
                                        story_id: data.story_id,
                                        task_id: data.id
                                    })
                        })
                            .then(res => res.json())
                            .then((data) => {
                                console.log(numResponses)
                                console.log(data);
                                numResponses++
                            })
                            .then(() => {
                                if (numResponses === devs.length){
                                    window.location.reload(false);
                                }
                            })
                    }
                    if (i === 0){
                        window.location.reload(false);
                    }
                }
            })
    }

    return (
        <div>
            <Modal
                {...props}
                size="md"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Create new Î™ssue
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <form className="row g-3">
                        <div className="col-12">
                            <label for="inputTittle" className="form-label">Issue Title <RedAsterisc/></label>
                            <input type="text" className="form-control" id="issueTittle" placeholder="Title" value={title} onChange={e => setTitle(e.target.value)}/>
                            <div>
                                { (showEmptyTitle) && <span class="badge bg-danger rounded-pill">Title can't be empty</span>}
                            </div>
                        </div>
                        <div className="col-12">
                            <label for="inputDescription" className="form-label">Issue Description <RedAsterisc/></label>
                            <textarea type="text" className="form-control" id="issueDescription" placeholder="Description" value={description} onChange={e => setDescription(e.target.value)}/>
                            <div>
                                { (showEmptyDescription) && <span class="badge bg-danger rounded-pill">Description can not be empty</span>}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <label for="inputIssueType" className="form-label">Issue Type <RedAsterisc/></label>
                            <select id="inputIssueType" className="form-select" value={selectedType} onChange={e => setIssTy(e.target.value)}>
                                <option selected>Select...</option>
                                {issueTypes.map(i => <option key={i}>{i}</option>)}
                            </select>
                        </div>
                        {(selectedType === 'Story') && (
                            <>
                            <div className="col-md-6"/>
                            <div className="col-md-6">
                                <label for="chooseEpic" className="form-label">Add to Epic</label>
                                <select id="chooseEpic" className="form-select" value={selectedEpic} onChange={e => setSelectedEpic(e.target.value)}>
                                    <option selected>Select...</option>
                                    {props.epics.map(i => <option key={i.id}>{i.title}</option>)}
                                </select>
                            </div>
                            <div className="col-md-6">
                                <label for="chooseSprint" className="form-label">Add to Sprint</label>
                                <select id="chooseSprint" className="form-select" value={selectedSprint} onChange={e => setSelectedSprint(e.target.value)}>
                                    <option selected>Select...</option>
                                    {sprints.map(i => <option key={i}>{i}</option>)}
                                </select>
                            </div>
                            </>
                        )}
                        {(selectedType === 'Task') && ( <>
                            <div className="col-md-6">
                                <label for="chooseStory" className="form-label">Assign to Story</label>
                                <select id="chooseStory" className="form-select" value={selectedStory} onChange={e => setSelectedStory(e.target.value)}>
                                    <option selected>Select...</option>
                                    {rawStories.map(i => <option key={i.id}>{i.title}</option>)}
                                </select>
                            </div>
                            <div className="col-12">
                                <label for="assignDev" className="form-label">Assign Dev</label>
                                <AssignDev devs={devs} setDevs={setDevs}  message=" will be assigned " checkForReAdd={false} projId={0}/>
                            </div>
                        </>)}
                    </form>
                    <div className="row g-3 pt-3">
                        <div className="col-8">
                            <button className="btn btn-primary" onClick={() => submit()}>Confirm</button>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="outline-danger" onClick={cancel}>Cancel</Button>
                </Modal.Footer>
            </Modal>
		</div>
    );
}
 
export default IssuePopUp;