import React, {useState, useEffect} from 'react';
import { useHistory } from 'react-router-dom'
import '../App.css';

function TaskWindow(props){

    const history = useHistory();
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));

    const [devs, setDevs] = useState([{id: 80, username: "nick_mk"},{id: 80, username: "alexa_michel"},{id: 80, username: "johhny_p"}]);

    useEffect(()=>{
        if (!loggedUser){
            history.push("/login");
        }
    },[props.focusTask])

    function markAsDone() {
        window.location.reload(false);
    }  

    function doneSatus(cond){
        if (cond){
            return (<span className="badge bg-success rounded-pill done-gradient">
                Done
            </span>);
        }
        return (<span className="badge bg-warning rounded-pill inprogress-gradient">In progress</span>);
    }

    return (
    <form className="row g-3 pt-4">
        <div className="col-12">
            <label for="inputDescription" className="form-label">Task Description: </label>
            <div>
                <small>{props.focusTask.description || "Pellentesque placerat in lectus ac commodo. Nam dolor augue, rutrum in magna vel, sollicitudin lobortis dolor. Maecenas in laoreet odio."}</small>
            </div>
        </div>
        <div className="col-12">
            <div>
                <label className="form-label">Story that task belongs to: </label>
            </div>
            <div className='badge bg-primary rounded-pill'>
                <small> {props.focusStory.title || "Story1"} </small>
            </div>
        </div>
        <div className="col-12">
            <div>
                <label className="form-label">Epic that story belongs to: </label>
            </div>
            <div className='badge bg-primary rounded-pill'>
                <small> {/*props.epic.title ||*/ "Epic1"} </small>
            </div>
        </div>
        <div className="col-12">
            <label className="form-label me-6">{"Status:   "}</label>
            {doneSatus(props.focusTask.status)}
        </div>
        <div className="col-12">
            <label for="assignDev" className="form-label">Responsibility of: </label>
            {devs.map(i => <div><span className="badge bg-primary rounded-pill cool-purple">{i.username}</span></div>
            )}
        </div>
        <div>
            {(props.focusTask.status === 0) && <div className="btn btn-success" onClick={() => markAsDone()}>Mark as Done</div>}
        </div>
    </form>);
}

export default TaskWindow;