import React, {useState, useEffect} from 'react';
import '../App.css';
import {Modal, Button} from 'react-bootstrap';
import AssignDev from './AssignDev.js'
import { useHistory } from 'react-router-dom'




function ProjectInfoPopUp(props){

    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));
    const history = useHistory();



    //const devs = ['alex','mich34','nikos','alejiz90']
    const [owner,setOwner] = useState(["jenny_molaki"])
    const [devs,setDevs] = useState([])
    const [devsPending,setDevsPending] = useState([])

    const [title, setTitle] = useState("")

    function archive() {
        fetch('http://localhost:8080/projects/' + props.projectId + '/archive/'+loggedUser.id , {
                method: 'put', 
                headers: { Authorization: 'Bearer ' + loggedUser.accessToken
                    }
            })
                .then(res => res.json())
                .then((data) => {
                    console.log(data) // expects {message:"OK"}
                    if (data.message){
                        if (data.message === "OK"){
                            window.location.reload(false);
                        }

                    }
                })
    }

    useEffect(() => {
        if (!loggedUser){
            history.push("/login");
        }
        else {
            if (props.show){
                setOwner(["jenny_molaki"])
                setDevs(["nick_mk", "alexa_michel", "johhny_p"])
                setDevsPending(["nick_digits"])
                // setTitle("")
                // setDescription("")
                // return data;
            }
        }
    },[props.show])

    function retDevs(){
        return devs.map(i => <div><span className="badge bg-primary rounded-pill cool-purple">{i}</span></div>)
    }

    return (
        <div>
            <Modal
                {...props}
                size="md"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Information on {props.projName}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <form className="row g-3">
                        <div className="col-12">
                            <label for="inputTittle" className="form-label">Product Owner:</label>
                            <div><span className="badge bg-primary rounded-pill cool-purple">{owner[0]}</span></div>
                        </div>
                        <div className="col-12">
                            <label className="form-label">Developers: </label>
                            {devs.map(i => <div><span className="badge bg-primary rounded-pill cool-purple">{i}</span></div>)}
                        </div>
                        <div className="col-12">
                            <label className="form-label">Developers that have not replied to invite: </label>
                            {devsPending.map(i => <div><span className="badge bg-primary rounded-pill cool-purple">{i}</span></div>)}
                        </div>
                    </form>
                    { !props.status && (loggedUser.id === loggedUser.id) &&
                        (<div className="row g-3 pt-3">
                            <div className="col-12 text-center">
                                <button className="btn btn-outline-warning text-center" onClick={() => archive()}>Archive</button>
                            </div>
                        </div>)
                    }
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="outline-danger" onClick={props.onHide}>Cancel</Button>
                </Modal.Footer>
            </Modal>
		</div>
    );
}
 
export default ProjectInfoPopUp;