import React, { useState, useEffect } from 'react';

import '../../App.css';
import SideNavBar from '../../components/SideNavBar.js'
import { Link, useHistory } from 'react-router-dom'
import Topbar from '../../components/Topbar.js'
import '../../css/settings.css';

function SettingsSecurityPage(){

    const history = useHistory();
    const loggedUser = JSON.parse(localStorage.getItem('loggedUser'));
    
    const [oldpass, setOldpass] = useState("");
    const [pass1, setPass1] = useState("");
    const [pass2, setPass2] = useState("");


    useEffect(() => {
        if (!loggedUser){
            history.push("/login");
        }
        document.body.style.background = "#fff";

    }, []);


    const [showEmptyOldPass, setShowEmptyOldPass] = useState(false)
    const [showEmptyPass1, setShowEmptyPass1] = useState(false)
    const [showEmptyPass2, setShowEmptyPass2] = useState(false)
    const [showPasswordsDontMatch, setShowPasswordsDontMatch] = useState(false)
    const [showBadPassInput, setShowBadPassInput] = useState(false)
    const [success, setSuccess] = useState(false)
    const [failure, setFailure] = useState(false)

    useEffect(() => {
        setShowPasswordsDontMatch(false);
    }, [pass1, pass2]);

    function passwordValid(str) {
        return /[a-z]/.test(str) && /[A-Z]/.test(str) && /\d/.test(str);
    }


    function sendPasswordChange() {
        let earlyExit = false

        if (!oldpass){
            console.log("oldpass empty");
            earlyExit = true;
            setShowEmptyOldPass(true);
        }else{
            setShowEmptyOldPass(false);
        }

        //Check pass1
        if (!pass1){//not empty
            console.log("pass1 is empty");
            earlyExit = true;
            setShowEmptyPass1(true);
        }
        else {
            if(!passwordValid(pass1)){//invalid chars
                earlyExit = true;
                setShowBadPassInput(true);
                setShowEmptyPass1(false);

            }else{
                setShowBadPassInput(false);
                setShowEmptyPass1(false);
            }
        }

        //check pass2
        if (!pass2){
            console.log("pass2 is empty");
            earlyExit = true;
            setShowEmptyPass2(true);
        }
        else {
            setShowEmptyPass2(false);
        }

        if (pass1 != pass2){
            console.log("passwords don't match");
            earlyExit = true;
            setShowPasswordsDontMatch(true);
        }

        

        if (earlyExit){
            return ;
        }
        setSuccess(false)
        setFailure(false)


        setSuccess(true)
        setOldpass("")
        setPass1("")
        setPass2("")

        // setFailure(true)

    }



    return (
        <div>
            <Topbar/>
            <SideNavBar/>
            <div className="mainContent">
                <div className="jete-settingsMenu settings-postition">
                    <div className="list-group">
                        <Link to='/settings/profile' className="list-group-item list-group-item-action">Profile</Link>
                        <a href="#" className="list-group-item list-group-item-action active">Security</a>
                        <Link to='/settings/billing' className="list-group-item list-group-item-action">Billing</Link>
                    </div>
                </div>
                <div className="settingsContains settings-postition">
                    <h1 className="settings-header">Account Security</h1>
                    <hr className="new4"/>
                    <p className="settings-unit">
                        <b>Change Password:</b>
                    </p>

                    <form className="settingsprofile-form change-password-form">
                            
                        <div className="form-group oldpassword">
                            <label for="settingsoldpassword">Current password:</label>
                            <input type="password" className="form-control" value={oldpass} id="settingsoldpassword" onChange={e => setOldpass(e.target.value)} placeholder="Type current password"/>
                            <div>
                                { (showEmptyOldPass) && <span className="badge bg-danger rounded-pill">Fill in your current password</span>}
                                { (success) && <span className="badge bg-primary rounded-pill">Change was successful</span>}
                                { (failure) && <span className="badge bg-danger rounded-pill">Wrong password</span>}
                            </div>
                        </div>
                            
                        <div className="form-group newpassword">
                            <label for="settingsnewpassword">New password:</label>
                            <input type="password" className="form-control"  value={pass1} id="settingsnewpassword"  onChange={e => setPass1(e.target.value)} placeholder="Type new password"/>
                            <div>
                                { (showBadPassInput) && <span className="badge bg-danger rounded-pill">Password needs to contain at least one capital letter and one digit</span>}
                            </div>
                            <div>
                                { (showEmptyPass1) && <span className="badge bg-danger rounded-pill">Password can't be blank</span>}
                            </div>
                        </div>
                            

                        <div className="form-group newpassword-reapeat">
                            <label for="settingsnewpassword-repeat">Confirm password:</label>
                            <input type="password" className="form-control" value={pass2}  id="settingsnewpassword-repeat" onChange={e => setPass2(e.target.value)} aria-describedby="pwhelp" placeholder="Type new password again"/>
                            <small id="pwhelp" className="form-text text-muted"><br/>Password needs to contain at least one capital letter and one digit</small>
                            <div>
                                { (showEmptyPass2) && <span className="badge bg-danger rounded-pill">Password can't be blank</span>}
                            </div>
                            <div>
                                { (showPasswordsDontMatch) && <span className="badge bg-danger rounded-pill">Passwords do not match</span>}
                            </div>
                        </div>

                        <button type="button" className="btn btn-primary passwordchange-button" onClick={sendPasswordChange}>Confirm</button>

                    </form>
                    <hr className="changepasswordline"/>
                    <p className="settings-unit">
                        <b>Verify email:</b>&emsp; &emsp; &emsp; Your email has been verified!
                    </p>
                    <div className="blankspace"/>                            
                </div>
            </div>
        </div>
    );
    
}
 
export default SettingsSecurityPage;